.divm {
    box-shadow: 0 0 5px black;
    background-color: rgba(233, 190, 190, 0.548);
    padding: 1%;
    border-radius: 10px;
    box-sizing: border-box;
    margin: 10px;
    width: calc(33.3% - 20px);
    min-width: 200px;
}
.img{
    max-width: 100%;
}

@media(max-width:990px){
    .divm {
        min-width: unset;
    }
}
@media(max-width:600px){
    .divm {
       width: calc(50% - 20px);
    }
}